.container {
  background: #000 url("../../static/images/background.png") no-repeat center top;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow-x: hidden;
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.logoRed {
  margin-top: 50px;
  margin-left: 40px;
  width: 207px;
  height: 111px;
}

.buttonsContainer {
  padding: 40px;
  vertical-align: center;
}

.homeTitle {
  width: 100%;
  height: 35vh;
  padding-left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

.homeTitle > p {
  min-width: 50vw;
  text-align: center;
  color: #FFFFFF;
  font-family: 'Montserrat Regular';
	font-size: 45px;
	letter-spacing: 0.9px;
	line-height: 58px;
}

.homeTitle > p > span {
  color: #fff;
  font-weight: bold;
}

.tryButton {
  outline: none;
  border: none;
  color: #fff;
  margin-top: 5px;
  font-size: 20px;
  background-color: #E32C3E;
  padding: 1.5rem 4rem;
  text-decoration: none;
}

.tryButton:hover {
  background-color: #800;
}

.mainFooter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: 2rem;
}

.mainFooter > span {
  margin: 0 2rem;
  opacity: 0.8;
  color: #fff;
  font-family: 'Roboto';	
  font-size: 24px;	
  font-weight: 300;	
  letter-spacing: 0.68px;	
  line-height: 28px;
}


@media (max-width: 500px) {
  .container {
    -ms-flex-align: center;
        align-items: center;
  }
  
  .logoRed {
    margin: 2rem 0rem 0rem 0rem;
    width: 150px;
    height: auto;
  }

  .tryButton {
    margin-top: 10px;
    font-size: 15px;
    padding: 1rem 2.5rem;
  }

  .homeTitle > p {
    font-size: 16px;
    letter-spacing: 0.9px;
    line-height: 29px;
  }

  .mainFooter > span {
    font-size: 16px;
  }
}


